import back from '@/components/back'
import noData from '@/components/noData'
export default {
    components: {
        back,
        noData
    },
    data(){
        return{
            page: 1,
            list: [],
            haveMore: true,
            user: this.$store.state.userInfo,
            pageShow: false
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        pull(h){
            this.$post('PlatFormAPI/ServicePackItem/PatientPackPayOrderPage',{
                PatientId: this.$route.query.patientId,
                DrId: this.user.Data.User.DoctorId,
                PageIndex: this.page,
                pageSize: 15
            }).then(o => {
                this.pageShow = true
                if (!o.ReturnData || o.ReturnData.length < 15){
                    this.haveMore = false
                }
                else {
                    this.haveMore = true
                }
                
                if (h){
                    o.ReturnData.forEach(v => {
                        this.list.push(v)
                    })
                }
                else {
                    this.list = o.ReturnData
                }
            })
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        toOrder(i){
            this.$router.push(`/service-package?orderId=${i.PayOrderId}`)
        }
    }
}



